
h1,
.h1 {
  color: $black;
  font-size: 72px;
  text-transform: uppercase;
  line-height: 1.1;
  letter-spacing: -2.5px;
  @include arial-b;

  @include md {
    font-size: 50px; }

  @include sm {
    font-size: 22px;
    letter-spacing: normal; } }

h2,
.h2 {
  font-size: 36px;
  text-transform: uppercase;
  @include arial-b;

  @include md {
    font-size: 28px; }

  @include sm {
    font-size: 20px; } }

h3,
.h3 {
  font-size: 26px;
  line-height: 1.5;
  text-transform: uppercase;
  @include arial-b;

  @include md {
    font-size: 22px; }

  @include sm {
    font-size: 18px; } }

h4,
.h4 {
  font-size: 20px;
  line-height: 1.2;
  text-transform: uppercase;
  @include arial-b;

  @include md {
    font-size: 18px; }

  @include sm {
    font-size: 16px; } }

h5,
.h5 {
  font-size: 16px;
  line-height: 1.2;

  @include sm {
    font-size: 15px; } }
