$header-padding: 20px;

.header {
  position: relative;
  padding: $header-padding;
  background: $gray;

  @include sm {
    padding: 10px 20px;
    @include clr; }

  &__logo {
    position: absolute;
    left: $header-padding;
    top: calc(#{$header-padding} + 3px);

    @include r(1300) {
      position: static; } }

  &__right {
    position: absolute;
    right: $header-padding;
    top: $header-padding;

    @include r(1300) {
      position: static;
      float: right;
      padding: 10px 0; }

    @include sm {
      padding: 5px 0; } }

  &__link {
    color: $black;
    font-size: 14px;
    @include tr(opacity .15s);

    &:not(:last-child) {
      margin-right: 55px;

      @include sm {
        margin-right: 15px; } }

    &:hover {
      opacity: 0.4; }

    @include sm {
      font-size: 10px; } }

  &__link_mod {
    color: rgba($black, 0.4); }

  .container {
    @include r(1300) {
      max-width: 100%;
      padding: 0;
      margin-top: 5px; }

    @include sm {
      margin-top: 0; } }

  .l {
    @include r(1300) {
      width: 100%;
      justify-content: space-between; } }

  .l__col {
    &:last-child {
      @include r(1300) {
        text-align: right;
        margin-left: 0; } }

    @include r(1300) {
      max-width: 50%;
      flex-basis: 50%;
      margin-bottom: 0; } } }
